.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

/* my styles */
@import url("https://fonts.googleapis.com/css2?family=Supermercado+One&display=swap");

* {
  font-family: "Supermercado One";
}
.calc_box {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  background-color: #13161b;
  min-height: 60vh;
  border-radius: 5%;
  padding: 2%;
  margin: -2%;
  max-width: min(70vw, 500px);
}

.number_box {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  min-height: 50vh;
  border-radius: 5%;
  padding: 2%;
  margin: -2%;
}

.nums {
  background-color: #6a0f38;
  color: white;
  flex: 30%;
  border: none;
  margin: 1%;
  border-radius: 5%;
  font-size: 2em;
}

.nums:hover {
  background-color: #892d56;
  color: rgb(243, 194, 194);
}

.symbol {
  display: flex;
  flex-direction: column-reverse;
}

.btn-symbol {
  flex-grow: 0.6;
  background-color: #4b0e26;
  color: rgb(206, 149, 149);
  border: none;
  margin: 3%;
  border-radius: 5%;
  padding-right: 2em;
  font-size: 1em;
}

.btn-symbol:hover {
  background-color: #e91973;
  color: rgb(228, 203, 203);
}

.btn-symbol:nth-child(1),
.btn-symbol:nth-child(5) {
  flex-grow: 2.2;
  background-color: #8b274d;
  color: #07080a;
}

.btn-symbol:nth-child(1):hover,
.btn-symbol:nth-child(5):hover {
  background-color: #e91973;
  color: rgb(228, 203, 203);
}

.entry {
  flex: 100%;
  height: 15vh;
  max-height: 32vh;
  width: 70vw;
  background-color: rgba(16, 13, 19, 0.699);
  margin: 0.2em;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  border-radius: 3%;
  color: #c0c9db;
  font-size: 4rem;
  overflow: hidden;
  overflow-wrap: break-word;
  word-break: break-all;
  resize: both;
}

.the_press_zone {
  display: flex;
  flex-direction: row;
  height: 33%;
}

div.symbol {
  flex-direction: row-reverse;
  margin: 2px;
  flex-wrap: wrap;
}
.btn-symbol {
  flex: 1;
  background-color: #4b0e26;
  color: rgb(206, 149, 149);
  border: none;
  margin: 1%;
  border-radius: 5%;
  padding-right: 2em;
  font-size: 1em;
}
.the_press_zone {
  display: flex;
  flex-direction: column-reverse;
}
.the_press_zone Symbol {
  flex-grow: 4;
  margin: -1em;
}
